import React from 'react'
import scooter from '../Assets/Images/delivery-motorcycle.jpg';
import '../css/Header.css';
import contact from '../Assets/Images/h_phone_bg.png'

function Header() {
    return(
        <div className="col-lg-12 col-md-12 col-sm-12 brand-header">
            <h2>EasY</h2>
            <div className="first-header">
                <hr />
                <div className="scooter-img">
                    <img src={scooter} alt="EG Inc. logo" />
                </div>
                <hr />
            </div>
            <div className="second-header">
                <div>
                    <h1>FOOD</h1>
                    <h2>DELIVERY</h2>
                    <div className="container">
                        <img src={contact}></img>
                        <div className="centered">+91 9538730361</div>
                    </div>
                </div>
            </div>
        </div>
    )
}

export default Header;