import React from 'react';
import { Navbar, Nav, Container } from 'react-bootstrap';
import { Link } from 'react-router-dom';
import Form from 'react-bootstrap/Form';
import {FormControl, Button } from 'react-bootstrap';
import { useLocation } from "react-router-dom";
import { FaPizzaSlice,FaUtensils } from 'react-icons/fa';
import './Navbar.css';
  
const Navigation = () => {
  //assigning location variable
  const location = useLocation();

  //destructuring pathname from location
  const { pathname } = location;

  //Javascript split method to get the name of the path in array
  const splitLocation = pathname.split("/egapp/");

  return(
    <header >
      <Navbar expand="sm" className="col-lg-12 col-sm-12 col-md-12 col-xs-12">
        <Container className="container">
          {/* <Navbar.Toggle aria-controls="responsive-navbar-nav" /> */}
          <Navbar id="responsive-navbar-nav">
            <Nav>
              <Link to= "/egapp/" className={splitLocation[1] === "" ? "active" : ""}><FaPizzaSlice/>Home</Link>
              <Link to= "/egapp/menu" className={splitLocation[1] === "menu" ? "active" : ""}><FaUtensils />Menu</Link>
            </Nav>
            {/* <Form inline>
              <FormControl type="text" placeholder="Search" className="mr-sm-2" />
              <Button variant="outline-success">Search</Button>
            </Form> */}
          </Navbar>
        </Container>
      </Navbar>
    </header>
  )
}

  export default Navigation;